.Cell {
	width: 100%;
	height: 100%;
	-webkit-user-select: none; 
	-webkit-touch-callout: none; 
	-moz-user-select: none; 
	-ms-user-select: none; 
	user-select: none;    
}

.CellButton {
	background-color: transparent;
	margin: 0;
	padding: 0;
	border:0;
	outline: none;
	position: relative;
}

.SBomb {
	height: 100%;
	width: 100%;
	position: absolute;
	background-color: rgba(238, 142, 142, 0.507);
}